<template>
  <div class="container user">
    <div class="head">
      <div class="info flex-center-y">
        <router-link to="user_info" class="portrait flex-center">
          <img :src="data.head_pic" />
        </router-link>
        <div class="order">
          <div class="flex-center-y">
            <span class="name">{{data.nickname}}</span>
            <span class="identity">{{data.level_name}}</span>
          </div>
          <p class="id">ID:{{data.user_no}}</p>
        </div>
      </div>

      <img src="../assets/images/my_message@2x.png" class="mesup" />

      <router-link to="setting">
        <img src="../assets/images/my_setting@2x.png" class="setup" />
      </router-link>
      <div class="points">
        <p>
          已为您赠送积分
          <span class="pointNum">{{data.pay_points}}</span>
        </p>
        <router-link to="signPoints">
          <span class="qiandao">签到领积分</span>
        </router-link>
      </div>
    </div>
    <div class="block flex column1">
      <router-link to="myOrder" class="item">
        <img src="../assets/images/my_payment@2x.png" alt />
        <span>待付款</span>
      </router-link>
      <router-link to="myOrder" class="item">
        <img src="../assets/images/my_delivery@2x.png" alt />
        <span>待发货</span>
      </router-link>
      <router-link to="myOrder" class="item">
        <img src="../assets/images/my_goods@2x.png" alt />
        <span>待收货</span>
      </router-link>
      <router-link to="myOrder" class="item">
        <img src="../assets/images/my_complete@2x.png" alt />
        <span>已完成</span>
      </router-link>
      <router-link to="myOrder" class="item">
        <img src="../assets/images/my_order@2x.png" alt />
        <span>我的订单</span>
      </router-link>
    </div>
    <div class="block flex-wrap column2">
      <router-link to="deviceMange" class="item">
        <img src="../assets/images/my_equipment@2x.png" alt />
        <span>设备管理</span>
      </router-link>
      <router-link to="teamOrder" class="item">
        <img src="../assets/images/my_Teamorders@2x.png" alt />
        <span>团队订单</span>
      </router-link>
      <router-link to="myTeam" class="item">
        <img src="../assets/images/my_Team@2x.png" alt />
        <span>我的团队</span>
      </router-link>
      <router-link to="dataCenter" class="item">
        <img src="../assets/images/my_data@2x.png" alt />
        <span>数据中心</span>
      </router-link>
      <router-link to="myCharge" class="item">
        <img src="../assets/images/my_change@2x.png" alt />
        <span>我的零钱</span>
      </router-link>
      <router-link
        :to="{name: 'myPoints',query: {is_jigou: data.is_jigou, jigou_status: data.jigou_status}}"
        class="item"
      >
        <img src="../assets/images/my_integral@2x.png" alt />
        <span>我的积分</span>
      </router-link>
      <router-link to="myCard" class="item">
        <img src="../assets/images/my_card@2x.png" alt />
        <span>我的维护卡</span>
      </router-link>
      <router-link to="myCoupon" class="item">
        <img src="../assets/images/my_coupons@2x.png" alt />
        <span>我的优惠券</span>
      </router-link>
      <router-link to="address" class="item">
        <img src="../assets/images/my_coupons@2x.png" alt />
        <span>地址管理</span>
      </router-link>
      <a class="item">
        <img src="../assets/images/my_help@2x.png" alt />
        <span>帮助中心</span>
      </a>
      <router-link to="invite" class="item">
        <img src="../assets/images/my_invitation@2x.png" alt />
        <span>邀请朋友</span>
      </router-link>
      <a class="item">
        <img src="../assets/images/my_contact@2x.png" alt />
        <span>联系客服</span>
      </a>
    </div>
  </div>
</template>

<script>
import { Icon } from "vant";
import axios from "axios";
import url from "@/axios/api.js";
export default {
  components: {
    "van-icon": Icon
  },
  data() {
    return {
      data: {}
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      let params = new URLSearchParams();

      params.append("openid", localStorage.getItem("openid"));
      axios.post(url.userInfo, params).then(res => {
        this.data = res.data.data;
      });
    }
  }
};
</script>

<style scoped lang="scss">
.user {
  background: url(../assets/images/my_bg@2x.png) no-repeat top;
  background-size: 100% 100%;
  height: 153px;
}
.head {
  padding: 20px 10px;
  position: relative;
  height: 100%;
  width: 100%;
  .points {
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 18px;
    left: 58px;
    p {
      font-size: 15px;
      font-weight: bold;
      color: #5e402b;
      .pointNum {
        font-size: 15px;
        font-weight: bold;
        color: rgba(230, 31, 31, 1);
      }
    }
    .qiandao {
      background: linear-gradient(
        90deg,
        rgba(251, 98, 75, 1),
        rgba(250, 98, 75, 1)
      );
      box-shadow: 0px 3px 5px 0px rgba(255, 129, 110, 0.5);
      padding: 5px 10px;
      border-radius: 11px;
      font-size: 13px;
      margin-left: 70px;
      color: #fff;
    }
  }
  .info {
    display: flex;
    align-items: center;
    .portrait {
      padding: 5px;
      border-radius: 50%;
      img {
        width: 53px;
        height: 53px;
        border-radius: 50%;
      }
    }
  }

  .order {
    color: #fff;
    margin-left: 5px;
    .name {
      font-size: 21px;
      font-family: SourceHanSansCN-Bold;
      font-weight: bold;
      color: rgba(255, 255, 255, 1);
      line-height: 13px;
    }
    .identity {
      padding: 2px 14px;
      background: #fff;
      border-radius: 30px;
      color: #2194e6;
      margin-left: 14px;
      background: rgba(0, 0, 0, 0.15);
      font-size: 11px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(255, 255, 255, 1);
      line-height: 13px;
    }
    .id {
      margin-top: 10px;
      font-size: 14px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(255, 255, 255, 1);
      line-height: 13px;
    }
  }
  .mesup {
    position: absolute;
    right: 40px;
    top: 33px;
    width: 20px;
  }
  .setup {
    position: absolute;
    right: 10px;
    top: 31px;
    width: 20px;
  }
  .signIn {
    position: absolute;
    right: -38px;
    top: 74px;
    height: 56px;
    background: rgba(255, 255, 255, 0.15);
    color: #fff;
    font-size: 28px;
    padding-left: 30px;
    padding-right: 10px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    border: 2px solid #6db6ff;
    border-right: 0;
    img {
      width: 29px;
    }
    p {
      margin: 0 4px 0 12px;
    }
    .arrow {
      font-size: 22px;
    }
  }
}
.block {
  background: #fff;
  display: flex;
  justify-content: space-around;
  .wallet {
    margin: 30px 0;
    img {
      width: 80px;
    }
    .text {
      margin-left: 25px;
      .num {
        font-size: 34px;
        font-weight: bold;
      }
      .txt {
        color: #939393;
        font-size: 24px;
      }
    }
  }
}
.column1 {
  .item {
    margin: 30px 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    img {
      width: 30px;
      margin-bottom: 10px;
    }
    span {
      font-size: 12px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(104, 104, 104, 1);
      line-height: 13px;
    }
  }
}
.column2 {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  .item {
    width: 25%;
    margin: 15px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    img {
      height: 30px;
      margin-bottom: 10px;
    }
    span {
      font-size: 12px;
      font-family: MicrosoftYaHei;
      font-weight: 400;
      color: rgba(51, 51, 51, 1);
      line-height: 13px;
    }
  }
}
</style>


